/* stylelint-enable */
.usa-prose > ul,
.usa-prose > ol, .usa-list {
  margin-bottom: 1em;
  margin-top: 1em;
  line-height: 1.5;
  padding-left: 3ch;
}
.usa-prose > ul:last-child,
.usa-prose > ol:last-child, .usa-list:last-child {
  margin-bottom: 0;
}
.usa-prose > ul ul,
.usa-prose > ol ul, .usa-list ul,
.usa-prose > ul ol,
.usa-prose > ol ol,
.usa-list ol {
  margin-top: 0.25em;
}

.usa-prose > ul li,
.usa-prose > ol li, .usa-list li {
  margin-bottom: 0.25em;
  max-width: 68ex;
}
.usa-prose > ul li:last-child,
.usa-prose > ol li:last-child, .usa-list li:last-child {
  margin-bottom: 0;
}

.usa-prose > table {
  font-family: Source Sans Pro Web, Helvetica Neue, Helvetica, Roboto, Arial, sans-serif;
  font-size: 1.06rem;
  line-height: 1.5;
  border-collapse: collapse;
  border-spacing: 0;
  margin: 1.25rem 0;
}
.usa-prose > table thead th {
  font-weight: 700;
}
.usa-prose > table thead th,
.usa-prose > table thead td {
  background-color: #f0f0f0;
}
.usa-prose > table th {
  text-align: left;
}
.usa-prose > table th,
.usa-prose > table td {
  border-width: 1px;
  border-color: #565c65;
  border-style: solid;
  background-color: white;
  font-weight: 400;
  padding: 0.5rem 1rem;
}
.usa-prose > table caption {
  font-family: Source Sans Pro Web, Helvetica Neue, Helvetica, Roboto, Arial, sans-serif;
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
  text-align: left;
}

.usa-prose > .usa-table--borderless thead th {
  background-color: transparent;
  border-top: 0;
}
.usa-prose > .usa-table--borderless th,
.usa-prose > .usa-table--borderless td {
  border-left: 0;
  border-right: 0;
}
.usa-prose > .usa-table--borderless th:first-child {
  padding-left: 0;
}

